import map from 'lodash/map'
import trim from 'lodash/trim'
import filter from 'lodash/filter'
import toUpper from 'lodash/toUpper'
import split from 'lodash/split'
import size from 'lodash/size'
import join from 'lodash/join'


/**
 * 
 *
 * @param {String} v 
 * @returns {String} 
 */
function seEncode(str) {

    //ks, 1~27
    let ks = '_abcdefghijklmnopqrstuvwxyzABCDEFGHIJKLMNOPQRSTUVWXYZ'

    //toUpper, 
    str = toUpper(str)

    //filter, 
    str = filter(str, function(v) {
        return v.match(/[A-Z]/)
    })

    //fks, 
    let fks = map(str, function(v) {
        return v.charCodeAt(0) - 64 //1~27
    })

    //c, , 2index, ks
    let c = ''
    for (let i = 0; i < fks.length; i += 2) {

        //k1
        let k1 = fks[i]

        //k2
        let k2 = 0
        let j = i + 1
        if (j < fks.length) {
            k2 = fks[j]
        }

        //k
        c += ks[k1 + k2]

    }

    return c
}


function seCut(str) {

    //split
    let s = split(str, '&')

    //trim
    s = map(s, function(v) {
        return trim(v)
    })

    //n
    let n = size(s)
    n = Math.min(4, n)

    //r
    let r = []
    for (let i = 0; i < n; i++) {
        r.push(s[i])
    }

    //join
    let c = join(r, '_')

    return c
}


function cvCasename(casename) {
    //return seEncode(casename)
    return seCut(casename)
}


export default cvCasename
